.container {
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;

  @media screen and (min-width: getBreakpoint("mobile")) {
    width: getBreakpoint("mobile");
  }

  @media screen and (min-width: getBreakpoint("tablet")) {
    width: getBreakpoint("tablet");
  }

  @media screen and (min-width: getBreakpoint("desktop")) {
    width: getBreakpoint("desktop");
  }
}
